  --container-wide: calc(var(--content-wide)  + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-dark-300: #0c224a;
  --color-dark-400: #1a1b24;
  --color-red-400: #e20403;
  --color-green-400: #378352;
  --color-green-300: #72c9cc;
  --color-gray-50: #fafafa;
  --color-gray-100: #b5b5b5;
  --color-gray-150: #919199;
  --color-blue-400: #0596e8;
  --color-gradient-main: linear-gradient(180deg, #80d0c8 0%, #0596e8 100%);
  --color-gradient-stock: linear-gradient(180deg, #7f99c6 0%, #00c3d0 100%);
  --color-gradient-crypto: linear-gradient(180deg, #d986c7 0%, #3c192c 100%);
  --color-gradient-side: linear-gradient(90deg, #aaa 0%, #fff0 100%);
  --color-gradient-border: linear-gradient(90deg, #fff0 0%, #fff 50.07%, #fff0 100%);
  --color-gradient-tab: linear-gradient(270deg, #fff0 0%, #aaa 52.31%, #fff0 100%);
  --color-gradient-forex: linear-gradient(180deg, #0d234a 0%, #6dc7cd 100%);
  --color-tab-active: linear-gradient(90deg, #0c99e7 0%, #fafafa00 55.49%);
  --color-calc: linear-gradient(270deg, #0c99e7 0%, #fafafa00 84.44%);
  --color-gradient-article: linear-gradient(270deg, #0000 0%, #000 51.56%, #0000 100%);
  --gradient-background: linear-gradient(0deg, #f5f7fa4d -49.83%, #c3cfe24d 100%);
  --gradient-wellcome: linear-gradient(90deg, #000 0%, #68a0f8 100%);
  --gradient-wellcome-shadow: linear-gradient(180deg, #0b1628 10%, #03256400 100%);
  --shadow-ligth: 0 0 6px #00000005, 0px 2px 4px #00000014;
  --radius-btn: 100px;
  --radius-main: 6px;
